<template>
  <v-container>
    <v-layout column>
      <Header />
      <v-divider></v-divider>
      <Body />
      <v-flex xs12>
        <h1>방명록</h1>
        <div class="error_field">{{ error }}</div>
        <v-layout row justify-center align-center>
          <v-flex xs12>
            <v-text-field
              v-model="name"
              label="이름"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <v-btn color='primary' v-on:click="this.add">추가하기</v-btn>
          </v-flex>
        </v-layout>
        <h2>본인취향 영화</h2>
        <v-radio-group row v-model="selection">
          <v-radio
            v-for="movie in movies"
            :key="movie.title"
            :label="`${movie.title}`"
            :value="movie.title"
          ></v-radio>
        </v-radio-group>
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="selects"
          class="elevation-1"
          hide-actions
          fix-header
        >
          <template v-slot:items="props">
            <td class="text-xs-center">{{ props.item.name }}</td>
            <td class="text-xs-center">{{ props.item.title }}</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Header from './Header'
import Body from './Body'

export default {
  components: {
    Header,
    Body
  },
  data: () => ({
    name: '',
    selection: '',
    error: '',
    movies: [
      { title: '어벤져스 : 엔드게임' },
      { title: '아이언맨1' },
      { title: '스파이더맨 홈 커밍' },
      { title: '데드풀1' },
      { title: '닥터스트레인지1' },
    ],
    headers: [
      { text: '이름', value: 'name', sortable: false, align: 'center', width: "20%" },
      { text: '선택 영화', value: 'title', sortable: false, align: 'center', width: "80%" },
    ],
    selects: [
    ]
  }),
  methods: {
    add() {
      if (this.name === '') {
        this.error = '이름을 입력해주세요'
        return
      }
      if (this.selection === '') {
        this.error = '영화를 선택해주세요'
        return
      }
      if (this.name !== '' && this.selection !== '') {
        var pushInfo = {
          name: this.name,
          title: this.selection
        }
        this.selects.push(pushInfo)
        this.clearInputs()
      }
    },
    clearInputs() {
      this.name = ''
      this.selection = ''
      this.error = ''
    }
  }
}
</script>

<style>
.error_field {
  color: red;
  background-color: 'white';
}
</style>
